.css-typing p {
    border-right: .15em solid white;
    font-family: "Courier";
    font-size: 40px;
    white-space: nowrap;
    overflow: hidden;
    margin: 0;
  }
  
  .text-container{
    display:flex;
    flex-wrap:wrap;
    color: white;
  }

  
  .css-typing .text-p-1 {
    width: 9em;
    animation: type 2s steps(40, end);
    animation-fill-mode: forwards;
  }
  
  .css-typing .text-p-2 {
    width: 25em;
    opacity: 0;
    animation: type2 4s steps(40, end);
    animation-delay: 2s;
    animation-fill-mode: forwards;
  }
  
  .css-typing .text-p-3 {
    width: 26em;
    opacity: 0;
    animation: type3 4s steps(40, end);
    animation-delay: 6s;
    animation-fill-mode: forwards;
  }
  
  .css-typing .text-p-4{
    width: 2.5cm;
    opacity: 0;
    animation: type3 0.1s steps(40, end);
    animation-delay: 10.3s;
    animation-fill-mode: forwards;
  }
  
  .css-typing .text-p-5 {
    width: 0.7cm;
    opacity: 0;
    animation: type4 0.1s steps(20, end), blink .5s step-end infinite alternate;
    animation-delay: 10.9s;
    animation-fill-mode: forwards;
  }


  .css-typing .text-p-6{
   
    width: 2.5cm;
    opacity: 0;
    animation: type3 0.1s steps(40, end);
    animation-delay: 12s;
    animation-fill-mode: forwards;
  }




  
  @keyframes type {
    0% {
      width: 0;
    }
    99.9% {
      border-right: .15em solid white;
    }
    100% {
      border: none;
    }
  }
  
  @keyframes type2 {
    0% {
      width: 0;
    }
    1% {
      opacity: 1;
    }
    99.9% {
      border-right: .15em solid white;
    }
    100% {
      opacity: 1;
      border: none;
    }
  }
  
  @keyframes type3 {
    0% {
      width: 0;
    }
    1% {
      opacity: 1;
    }
    99.9% {
      border-right: .15em solid white;
    }
    100% {
      opacity: 1;
      border: none;
    }
  }
  
  @keyframes type4 {
    0% {
      width: 0;
    }
    1% {
      opacity: 1;
    }
    100% {
      opacity: 1;
    }
  }
  
  @keyframes type5 {
    0% {
      width: 0;
    }
    1% {
      opacity: 1;
    }
    100% {
      opacity: 1;
    }
  }
  
  @keyframes blink {
    50% {
      border-color: transparent;
    }
  }


  



  /* arrow animation  */

  .text-container2{
    padding-left: 50%;
  }

  .containerArrow {
    position: relative;
    width: 25px;
    height: 80px;
    animation-delay: 10.9s;
    padding-top: 20px;
    padding-right: 100px;
    
  }
  
  .chevron {
    position: absolute;
    width: 28px;
    height: 8px;
    opacity: 0;
    transform: scale3d(0.5, 0.5, 0.5);
    animation: move 3s ease-out infinite;
  }
  
  .chevron:first-child {
    animation: move 3s ease-out 1s infinite;
  }
  
  .chevron:nth-child(2) {
    animation: move 3s ease-out 2s infinite;
  }
  
  .chevron:before,
  .chevron:after {
    content: ' ';
    position: absolute;
    top: 0;
    height: 100%;
    width: 51%;
    background: #fff;
  }
  
  .chevron:before {
    left: 0;
    transform: skew(0deg, 30deg);
  }
  
  .chevron:after {
    right: 0;
    width: 50%;
    transform: skew(0deg, -30deg);
  }
  
  @keyframes move {
    25% {
      opacity: 1;
  
    }
    33% {
      opacity: 1;
      transform: translateY(30px);
    }
    67% {
      opacity: 1;
      transform: translateY(40px);
    }
    100% {
      opacity: 0;
      transform: translateY(55px) scale3d(0.5, 0.5, 0.5);
    }
  }
  
